
.doc {
  @include xs-block {
    width: 100%;
    flex-direction: column;
    margin-bottom: rem(120);
    margin-top: 0;
  }
  @include lg-block {
    width: 94%;
    margin-top: rem(120);
  }

  .btn_up {
    box-sizing: content-box;
    position: fixed;
    border: 1px solid #000;
    border-radius: 80px;
    background: #fff; 

    @include xs-block {
      display: none;
    }
    @include lg-block {
      opacity: 0;
      display: none;
      bottom: 2vh;
      right: 5vw;
      transition: all 0.2s ease-in-out;
      animation: show_btn 0.5s ease-in-out;

    }
    @media screen and (min-width: 1200px) and (max-height: 800px) {
      right: 15vw;
    }

    @include  xl-block {
      right: 10vw;
    }
    @media screen and (min-width: 1920px) {
      right: 15vw;
    }

    &:hover {
      filter: invert(100%);
      transform: scale(1.2);
      transition: all 0.3s ease-in-out;
    }

    img{
      box-sizing: content-box;
      transform: rotate(-90deg);
      width: rem(15);
      padding: rem(10);
      height: auto;
      transition: all 0.2s ease-in-out;

      &:hover {
        transform: scale(1.3) rotate(-90deg);
        transition: all 0.3s ease-in-out;
      }
    } 
  }


   .show_btn {
     @include lg-block {
       display: block;
       opacity: 1;
       animation: show_btn 0.5s ease-in-out;

     }
   }


  .doc__title {
    display: flex;
    flex-direction: column;
    @include xs-block {
      align-items: center;
      margin-bottom: rem(30);
    }
   
    @include lg-block {
      margin-top: rem(71);
      align-items: flex-start;
      margin-bottom: rem(0);
    }

      h1 {
       font-size: rem(48);
       @include xs-block {
        order: 1;
        text-align: center;
      }
       @include lg-block{
        text-align: left;
        margin-bottom: rem(30);
       }

      }


      .comment { 
        font-size: rem(22);
        line-height: rem(34);
        color: #9297A2;
        font-weight: 300;

        @include xs-block {
          order: 2;
          position: relative;
          top: 0;
          font-size: rem(16);
          line-height: rem(34);
          margin: 0;
        }
        @include lg-block {
          font-size: rem(22);
          margin-bottom: rem(30);
        } 
      }
  }

  .cookie_title {
    display: flex;
    flex-direction: column;

    @include xs-block {
      margin-bottom: rem(30);
      align-items: center;
    }
    @include lg-block {
      margin-top: rem(71);
      align-items: flex-start;
      margin-bottom: rem(0);
    }

    h1 {
      font-size: rem(48);
      @include xs-block {
        text-align: center; 
      }
      @include lg-block {
        margin-bottom: rem(20);
        text-align: left;
        line-height: rem(54);
      }
     }
     .comment { 
      @include xs-block {
        order: 2;
        position: relative;
        top: 0;
        font-size: rem(16);
        line-height: rem(34);
        margin: 0;
      }
      @include lg-block {
        font-size: rem(22);
        line-height: rem(34);
        text-align: left;
        margin-bottom: rem(20);
      }
      font-size: rem(22);
      line-height: rem(34);
      color: #9297A2;
      font-weight: 300;
      
    }
     

  }
  
  .doc__text {
    font-size: rem(18);
    line-height: rem(24);
    font-weight: 300;

    ul {
      list-style-type: disc;
      padding-left: 50px;
  
    }



    a {
      color: #0072FF;
      &:hover {
          text-decoration-line: underline;
      }
    }
  }


  p {
    margin: rem(16) 0;
  }


  ol {
    @include xs-block {
      padding-left: rem(10);
    }
    @include lg-block {
      padding-left: rem(15);
    }
  
  }

li {
  margin-bottom: rem(16);
}
.list {
  list-style-type: decimal;
  
  li {
    font-weight: 700;
  }
  ol {
    margin-top: 16px;
    list-style-type: decimal;
    li {
      font-weight: 300;
    }
  }
}

.address {
  margin-top: rem(60);
  .address_disc {
    margin-bottom: rem(16);
  }
  p {
    margin: 0;
  }
}

.p_p_content {
  h3, h4 {
    margin: rem(16) 0;
  }
  ul, ol {
    padding-left: 0px;

    li {
      margin-left: rem(20);
    }
  }

  ol {
    list-style-type: decimal;
  }
}
.subtitle {
  list-style-type: none;
}

}

.xs {
  @include xs(display, block);
}

.xs_n { 
  @include xs(display, none);
}


 
