@include xl-block {
  .footer {
    display: flex;
    flex-direction: column;
  }
}
.footer {
  .footer_xl {
    @include sm(display, block);
    @include xs(display, none);

    @include sm-block {
      .footer__navigation {
        @include sm-block{
          margin-top: rem(40);
        }
        display: flex;
        flex-direction: column;
        align-items: center;
        margin-bottom: rem(20);
        margin-top: rem(120);
        width: 100%;

        .footer__navigation_link {
          order: 2;
          align-self: center;
          ul {
            display: flex;
            flex-direction: row;
            justify-content: flex-start;
            flex-wrap: nowrap;

            li {
              margin-right: rem(10);
              a {
                font-size: rem(20);
                font-weight: 300;
              }
            }
          }
        }

        .footer__socials {
          display: flex;
          flex-wrap: nowrap;
          justify-content: space-around;
          order: 1;
          margin-bottom: rem(40);
          width: rem(200);
          height: rem(60);
          img {
            width: rem(60);
            height: auto;
          }
        }
      }
      .footer_copyright {
        text-align: center;
        font-size: rem(12);
        margin-bottom: rem(30);
      }
    }

    @include md-block {
      .footer__navigation {
        flex-direction: row;
        justify-content: space-between;
        .footer__navigation_link {
          align-self: flex-start;
          order: 1;
          ul {
            align-items: center;
            display: flex;
            flex-direction: row;
            justify-content: flex-start;
            flex-wrap: nowrap;
            li {
              margin-right: rem(10);
              a {
                font-size: rem(17);
                font-weight: 300;
                line-height: rem(36);
              }
            }
          }
        }

        .footer__socials {
          display: flex;
          flex-wrap: nowrap;
          justify-content: space-around;
          margin-bottom: 0px;
          width: rem(100);
          height: rem(60);
          order: 2;
          img {
            width: rem(36);
            height: auto;
          }
        }
      }
      .footer_copyright {
        text-align: left;
        font-size: rem(12);
        margin-bottom: rem(30);
      }
    }

    @include lg-block {
      .footer__navigation {
        margin-top: 0;
        .footer__navigation_link {
          ul {
            li {
              a {
                font-size: rem(16);
              }
            }
          }
        }

        .footer__socials {
          width: rem(140);
        }
      }
    }

    @include xl-block {
      .footer__navigation {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        margin-bottom: rem(20);
      }

      .footer__navigation_link ul {
        display: flex;
        flex-direction: row;
        align-self: center;

        li:first-child {
          padding-left: 0;
        }

        li a {
          font-size: rem(18);
          word-wrap: normal;
          font-weight: 300;
        }
      }

      .footer__socials {
        display: flex;
        justify-content: space-between;
        height: rem(36);
        max-width: rem(210);

        a {
          padding: 0;
        }
      }

      .footer_copyright {
        font-size: rem(14);
      }
    }
  }

  .footer_xs {
    @include sm(display, none);

    @include xs-block {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      align-items: center;

      .nav {
        width: 100%;
        display: none;
        flex-direction: column;
        justify-content: space-around;
        text-align: center;
        font-size: 28px;
        font-weight: 700;
        margin-bottom: 60px;

        li {
          border-bottom: 2px solid lightgrey;
          padding: 40px 0;
        }
      }

      .socials {
        display: flex;
        justify-content: space-between;
        width: 185px;
        margin: 0 auto 80px;
        img {
          width: 60px;
          height: auto;
        }
      }

      .footer_text {
        display: flex;
        justify-content: space-evenly;
        width: 100%;
        margin-bottom: 30px;
        li {
          a {
            font-size: 13px;
          }
        }
      }

      .footer_copyright {
        text-align: center;
        font-size: 12px;
        margin-bottom: 30px;
      }
    }
  }
}

@include lg-block {
  footer {
    .footer {
      .footer_xl {
        position: relative;
        .footer__navigation {
          margin-top: 0;
        }
      }
    }
  }
}
