@mixin featchest_animation {
    transform: translateY(-100px);
    animation: slide-bottom 2s cubic-bezier(0.165, 0.840, 0.440, 1.000) forwards;
    opacity: 0;
  
}

@keyframes slide-bottom {
  0% {
            transform: translateY(-100px);
            opacity: 0;       
  }

  30% {
            transform: translateY(-20px);
            opacity: 0;         
  }

  100% {
            transform: translateY(0px);
            opacity: 1;
  }
}

.featchest_animation {
    &:nth-child(1) {
      @include featchest_animation();
  
    }

    &:nth-child(2) {
      @include featchest_animation();
            animation-delay: 1s;
    }

    &:nth-child(3) {
      @include featchest_animation();
      animation-delay: 2s;
    }
}


@keyframes show_btn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}


