#download {
  $space_y: 320;
  position: relative;

  @include lg-block {
    height: 100vh;
    background: no-repeat url('../img/svg/main_bg.svg');
    background-position-y: -22vh;
    background-size: rem(1400);
  }
  @include xl-block {
    background-size: rem(1700);
    background-position-y: rem(-$space_y);
  }

  @for $i from 0 through 4 {
    $space_x_800: 150px;
    $width_992: 992px;
    @media screen and (max-height: 800px) and (min-width: $width_992 + $i * 50) {
      background-position-x: $space_x_800 + $i * 22;
    }
  }

  @for $i from 0 through 14 {
    $space_x: 220;
    $width_rem: 1200;
    @media screen and (min-height: 800px) and (min-width: rem($width_rem + ($i * 50))) {
      background-size: rem(1570);
      background-position-x: rem($space_x + ($i * 33));
    }
  }

  @for $i from 0 through 14 {
    $width_1920: 1920px;
    $space_x_1920: 530;
    @media screen and (min-height: 800px) and (min-width: $width_1920 + $i * 50) {
      background-position-x: rem($space_x_1920 + ($i * 33));
    }
  }

  @for $i from 0 through 14 {
    $space_x_1: 330;
    $width: 1200px;
    @media screen and (max-height: 800px) and (min-width: $width + $i * 50) {
      background-size: rem(1700);
      background-position-x: rem($space_x_1 + ($i * 35));
    }
  }

  @for $i from 0 through 24 {
    $space_x_4k: 450;
    $width_4k: 3940px;
    @media screen and (min-height: 800px) and (min-width: $width_4k + $i * 50) {
      background-size: rem(1700);
      background-position-x: rem($space_x_4k + ($i * 11));
    }
  }
}

.download {
  position: relative;

  @include xs-block {
    height: max-content;
  }
  @include lg-block {
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
  .header {
    @include xs-block {
      display: none;
    }
    @include lg-block {
      display: flex;
    }
  }

  .content {
    @include xs-block {
      display: inherit;
    }
    @include lg-block {
      flex-grow: 1;
      display: flex;
      flex-direction: row;
      margin-top: rem($mr_from_header);
    }
  }

  .description {
    @include xs-block {
      display: flex;
      flex-direction: column;
      align-items: center;
      margin-top: 0px;
    }
    @include size-md(12);
    @include lg-block {
      align-items: flex-start;
      width: 44%;
      margin-top: 0px;
    }
  }
  .title {
    word-break: keep-all;
    @include xs-block {
      margin-top: 0;
    }
  }

  .logo_xs {
    @include xs-block {
      margin-top: 120px;
      display: flex;
      width: 90px;
      margin-bottom: 30px;
      img {
        width: 90px;
        height: auto;
      }
    }
    @include lg-block {
      display: none;
    }
  }

  .downloads_description_xs {
    @include xs-block {
      margin-top: 20px;
    }
  }

  .downloads_icons {
    width: rem(240);
    @include xs-block {
      align-self: center;
      img {
        width: rem(200);
        height: auto;
        margin-bottom: rem(29.01);
      }
      @include sm-block {
        margin-bottom: rem(120);
      }
      @include lg-block {
        align-self: flex-start;
      }
    }

    img {
      width: rem(240);
      height: auto;
      margin-bottom: rem(40.93);
    }
  }
}
