.index_logo {
  @include lg-block {
    position: absolute;
    top: rem(-20);
  }
  @include xl-block {
    top: rem(-35);
  }
  @include lg-block {
    width:rem(390);
    height:rem(390);
    a {
      height: 100%;
      width: 100%;
      img{
        height: 100%;
        width: 100%;
      }
    }
  }
  @include xl-block {
    width:rem(420);
    height:rem(420);
  }
}

.index_lang {
  @include lg-block {
    left: 80%;
  }
}


.title {
  font-weight: 900;
  word-break: break-word;

  @include xs-block {
    font-size: 58px;
    text-align: center;
    margin-bottom: 60px;
    letter-spacing: -1.392px;

  }
  @include sm-block {
    word-wrap: wrap;
    margin-bottom: rem(60);
  }
  @include lg-block {
    text-align: left;
    font-size: 4.5rem;
    letter-spacing: -2.378px;
  }
  @include lg-block {
    margin-bottom: rem(120);
  }
}

.downloads_icons {
  @include xs-block {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-self: flex-start;
 
    img {
      width: 250px;
      height: 80px;
      margin-bottom: 40px;
    }
  }

  @include sm-block {
    width: 100%;
    justify-content: space-around;
    margin-bottom: 0;
    height: 60px;
    img {
      width: 220px;
      height: 58px;
    }
  }

  @include md-block {
    margin: 0 auto;
    width: 600px;
    justify-content: space-evenly;
    margin-bottom: 0;
    height: 60px;
    img {
      width: auto;
      height: 70px;
    }
  }

  @include lg-block {
    width: rem(362);
    height: rem(48);
    justify-content: space-between;
    margin: 0;
    align-self: flex-start;
    img {
      width: auto;
      height: rem(48);
    }
  }

  @include xl-block {
    width: rem(442);
    justify-content: space-between;
    margin: 0;
    img {
      width: auto;
      height: rem(58);
    }
  }
}

#about {
  @include lg-block {
    height: 100vh;
    overflow-y: auto;
  }
}

.about {
  @include xs-block {
    position: relative;
  }
  @include sm-block {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
  @include lg-block {
    height: 100vh;
    position: relative;
  }
  .content {
    @include xs-block {
      padding-bottom: 40px;
    }
    @include lg-block {
      padding-bottom: 0;
      display: flex;
      flex-direction: row;
      flex-wrap: nowrap;
      justify-content: space-between;
      flex-grow: 1;
      margin-top: rem(20);
    }

    .description {
      position: relative;

      @include xs-block {
        display: flex;
        flex-flow: nowrap column;
        justify-content: flex-start;
        align-items: center;
        height: 100%;
      }
      @include lg-block {
        align-items: flex-start;
        width: 44%;
        margin-top: rem(200);
        height: auto;
      }
      @include xl-block {
        position: static;
        align-items: flex-start;
      }
    }
    .viwe {
      @include xs-block {
        margin-top: 40px;
        height: 350px;
      }
      @include sm-block {
        height:  560px;
        margin-top: 80px;
      }
      @include md-block {
        height:  754px;
      }
      @include size-lg(6);
      @include lg-block {
        margin-top: rem(20);
        display: inherit;
        margin-right: rem(50);
        height: rem(600);
      }
      @include xl-block {
        height: rem(500);
      }
      img {
        object-fit: contain;
        @include xs-block {
          width: 100%;

        }
        @include lg-block {
          height: rem(500);
          object-position: center;
        }
      }
    }
  }
  .footer {
    @include lg-block {
      margin-top: rem(80);
    }
  }
}
