@mixin feach_icon {
  position: absolute;
  left: -2%;
  top: -10px;
  display: inline-block;
  background: red;
  border-radius: 50%;
  width: rem(32);
  height: rem(32);
  color: white;
  font-size: rem(18);
  line-height: rem(32);
  text-align: center;
}

@mixin description {
  width: 50%;
  display: flex;
  flex-direction: column;
}

@mixin section {
  display: flex;
  flex-direction: row;
  box-sizing: border-box;
}

@mixin action_item {
  display: inline-block;
  width: rem(50);
  height: rem(50);
  color: #fff;
  background: #000;
  font-weight: bold;
  font-size: rem(26);
  border-radius: rem(25);
  line-height: rem(50);
  text-align: center;
  position: absolute;
  left: rem(27);
  top: rem(25);
}

@mixin hyvw__item_icon {
  display: inline-block;
  box-sizing: border-box;
  position: absolute;
  left: 0;
  font-size: rem(40);
  width: rem(90);
  height: rem(90);
  border: rem(10) solid #f2f2f2;
  border-radius: rem(28);
  line-height: rem(70);
  text-align: center;
}

@mixin container-full_doc() {
  padding-left: 0;
  padding-right: 0;
}

@mixin container_doc() {
  max-width: 1280px;
  margin: 0 auto;
  @include container-full_doc();
}

@mixin spiner_typing {
  display: inline-block;
  width: 2px;
  height: 2px;
  border-radius: 10px;
  background: #fff;
  @for $i from 1 through 3 {
    &:nth-child(#{$i}) {
      animation: 1.5s typing_spiner infinite cubic-bezier(0.550, 0.085, 0.680, 0.530) ($i * .3333s);
    }
  }
}

@mixin chat_message {
  margin-top: rem(30);
  padding: rem(10);
  border-radius: rem(17.75);
  display: inline-block;
}

@mixin typing {
  @include chat_message();
  overflow-x: hidden;
  white-space: nowrap;
  font-weight: 300;
  position: absolute;
  
  color: #fff;
  opacity: 0;
  font-size: rem(20);
}

@mixin feache_bg {
  background-position: rem(25) center;
  @include lg-block {
    background-size: 19%;
  }
  @include xl-block {
    background-size: 13%;
  }
}
